.button {
  display: inline-flex;
  padding: 15px 20px;
  color: $color-default-white;
  background-color: $color-navy-blue;
  border: none;
  cursor: pointer;
  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}
